const uni = require('uni').default;


























var app= require("../../common/common.js"); 
var id;
export default{
	data:{
		pageLoad:false, 
		pageData:{}
	},
	onLoad:function(option){
		id=option.id;
		this.getPage();
	},
	 
	methods:{
		getPage:function(){
			var that=this;
			uni.request({
				url:app.apiHost+"?m=user&a=set&ajax=1",
				data:{
					authcode: app.getAuthCode(),
					fromapp:app.fromapp()
				},
				success:function(data){
					if(data.data.error){
						app.goHome();
					}else{
						that.pageLoad=true;
						that.pageData=data.data.data;
					}
					
					 
				}
			})
		},
		gourl:function(url){
			uni.navigateTo({
				url:url,
			})
		},
		loginOut:function(){
			 
			uni.request({
				url:app.apiHost+"?m=login&a=logout&ajax=1",
				data:{
					"authcode":app.getAuthCode(),
					"fromapp":app.fromapp()
				},
				success:function(res){
					app.setAuthCode("");
					app.goHome();	
				}
			})
		}
	},
}



